<template>
  <div class="search_tips" v-show="tipsDisplay">
    <div class="tips_result_list">
      <ul>
        <li
          :class="{search_tips_list_hover:item.select}"
          v-for="item in tipsResultList"
          :key="item.title"
          @click="tipsClick(item.title)"
          @mouseenter="onMouseEnter"
        >
          <span v-text="item.title"></span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// import Vue from "vue";
import serachService from "../service/protocol";
import tools from "../service/tools";

const vmDataModel = {
  tipsResultList: [],
  tipsDisplay: false,
  indexPoint: -1
};

// 当单击其他地方时 自动隐藏提示框
document.getElementsByTagName("body")[0].onclick = function() {
  vmDataModel.tipsDisplay = false;
};

export default {
  name: "SearchTips",
  props: {
    keywordCallback: Function,
    keyword: String
  },
  data() {
    return vmDataModel;
  },
  methods: {
    tipsClick(item) {
      this.keywordCallback(item);
      this.tipsDisplay = false;
      this.indexPoint = -1;
    },
    tipsListOperate(operate) {
      // 某些情况下不触发这个
      if (!this.tipsDisplay || this.tipsResultList.length == 0) {
        return;
      }
      // 首先默认全部未未选中
      for (const v of this.tipsResultList) {
        v.select = false;
      }
      // 回车选择
      if (operate === "enter" && this.tipsResultList[this.indexPoint]) {
        this.tipsClick(this.tipsResultList[this.indexPoint].title);
        return;
      }

      // 上下键操作
      if (operate === "up" && this.indexPoint > 0) {
        this.indexPoint--;
        this.tipsResultList[this.indexPoint].select = true;
      } else if (
        operate === "down" &&
        this.indexPoint < this.tipsResultList.length - 1
      ) {
        this.indexPoint++;
        this.tipsResultList[this.indexPoint].select = true;
      } else if (this.indexPoint <= 0 && operate === "up") {
        this.indexPoint = this.tipsResultList.length - 1;
      } else if (
        this.indexPoint >= this.tipsResultList.length - 1 &&
        operate === "down"
      ) {
        this.indexPoint = 0;
      }

      //设置被选择并且传递给父组件
      if (this.tipsResultList[this.indexPoint]) {
        this.tipsResultList[this.indexPoint].select = true;
        this.keywordCallback(this.tipsResultList[this.indexPoint].title);
      }
    },
    onMouseEnter() {
      for (const v of this.tipsResultList) {
        v.select = false;
      }
      this.indexPoint = -1;
    },
    setTipsDisplay(bool) {
      this.tipsDisplay = bool;
    },
    //父组件 输入事件
    keywordInputEvent(keyword) {
      this.indexPoint = -1;
      tools.debounce(async () => {
        if (keyword.trim() !== "") {
          const resObject = await serachService.keywordTips(keyword);
          const list = resObject.s;
          this.tipsResultList = [];
          let i = 0;
          for (const v of list) {
            if (i > 7) break;
            this.tipsResultList.push({
              title: v,
              select: false
            });
            i++;
          }
          if (this.tipsResultList.length > 0) this.tipsDisplay = true;
          return;
        }
        this.tipsDisplay = false;
      }, 100);
    }
  },
  watch: {
    // keyword(val) {}
  }
};
</script>

<style scoped>
.search_tips_list_hover {
  background-color: rgb(221, 221, 221);
}

.search_tips {
  text-align: left;
  font-size: 14px;
  font-weight: 400;
  border: 1px solid #b9b9b9;
  /* 抵消按钮之间的距离 */
  margin: 0px 4px;
  margin-bottom: -18px;
}
.search_tips ul {
  padding: 0px;
  margin: 0px;
}
.search_tips li {
  list-style-type: none;
  cursor: pointer;
  padding-left: 6px;
  color: #000;
  position: relative;
  line-height: 28px;
  height: 28px;
  transition: all 0.2s;
}
.search_tips li:hover {
  background-color: rgb(211, 211, 211);
}
</style>
