<template>
  <div class="search_button_wapper">
    <div class="search_btn">
      <button @click="toButtonAction('baidu')">百度搜索</button>
      <button @click="toButtonAction('bing','0')">必应搜索</button>
      <button @click="toButtonAction('tieba','tieba.com')">贴吧搜索</button>
      <button @click="toButtonAction('zhihu')">知乎搜索</button>
      <button @click="toButtonAction('google')">Google 搜索</button>
      <button @click="toButtonAction('translate')">Google 翻译</button>
    </div>
  </div>
</template>

<script>
const vmDataModel = {};

import searchService from "../service/protocol";

export default {
  name: "SearchButton",
  props: {
    keyword: String
  },
  data() {
    return vmDataModel;
  },
  methods: {
    toButtonAction(type, args) {
      //  keywordSearch(type, keyword, args)
      const targetURL = searchService.keywordSearch(type, this.keyword, [args]);
      window.location = targetURL;
    }
  }
};
</script>

<style scoped>
.search_btn > button {
  background-color: #1e1e1f;
  border: 1px solid #505152;
  box-shadow: 0 0 2px #5f5d5d;
  color: #fff;
  margin: 4px;
  width: 100px;
  min-height: 42px;
  cursor: pointer;
  font-size: 13px;
  text-align: center;
  transition: all 0.4s;
}

.search_btn > button:hover {
  margin: 4px;
  width: 110px;
  background-color: rgb(57, 181, 74);
  border: 1px solid rgb(85, 85, 85);
  box-shadow: 0 0 2px rgb(57, 181, 74);
}

.search_button_wapper {
  margin-top: 20px;
}

@media (max-width: 768px) {
  .search_btn > button {
    max-width: 94px;
  }
}
</style>
