<template>
  <div class="search_wapper">
    <!-- 标题 -->
    <div class="search_box_logo"></div>
    <div class="search_box_context">
      <!-- 搜索框 -->
      <div class="input_wapper" @click="selectBoxAnimationEvent" @mouseleave="onMouseLeave">
        <input
          id="search_keyword"
          autocomplete="off"
          placeholder="你好世界 | Hello World | Привет мир | Bonjour le monde | こんにちは世界 | Ciao mondo | Hallo Welt | 안녕 세상 | Saluton mondo."
          v-model="visualKeyword"
          @input="keywordInput"
          v-on:keyup.up="keywordOperate('up')"
          v-on:keyup.down="keywordOperate('down')"
          v-on:keyup.enter="keywordOperate('enter')"
          @click="onKeywordInputClick"
          onfocus="this.select()"
        />
      </div>
      <!-- 搜索提示框 -->
      <SearchTips :keyword="keyword" :keywordCallback="setkeyword" ref="refSearchTips" />
    </div>
    <!-- 搜索按钮组 -->
    <SearchButton :keyword="keyword" />
  </div>
</template>

<script>
import SearchButton from "./SearchButton";
import SearchTips from "./SearchTips";
import searchService from "../service/protocol";

// 这里是直接使用库
import VueCookies from "vue-cookies";

const vmDataModel = {
  keyword: "",
  tipsDisplay: true,
  visualKeyword: ""
};

// 获取上次搜索记录
VueCookies.config("1h");
const historyKeyword = VueCookies.get("keyword") || "";
if (historyKeyword) {
  VueCookies.remove("keyword", null);
  vmDataModel.keyword = vmDataModel.visualKeyword = historyKeyword;
}

export default {
  name: "SearchBox",
  props: {
    onKeywordInputClickEvent: Function,
    onkeywordInputEvent: Function
  },
  components: {
    SearchButton,
    SearchTips
  },
  data() {
    return vmDataModel;
  },
  methods: {
    setkeyword(text) {
      this.keyword = this.visualKeyword = text;
    },
    keywordInput() {
      this.keyword = this.visualKeyword;
      this.onkeywordInputEvent && this.onkeywordInputEvent(this.keyword);
      this.$refs.refSearchTips.keywordInputEvent(this.keyword);
    },
    keywordOperate(operate) {
      if (operate === "enter") {
        const targetURL = searchService.keywordSearch("bing", this.keyword, [
          0
        ]);
        window.location = targetURL;
        return;
      }
      this.$refs.refSearchTips.tipsListOperate(operate);
    },
    onKeywordInputClick() {
      this.onKeywordInputClickEvent && this.onKeywordInputClickEvent();
      // document.getElementById("page_body").style.backgroundColor = "#e1e1e1";
    },
    onMouseLeave() {
      // document.getElementById("page_body").style.backgroundColor = "#FFFFFF";
    },
    selectBoxAnimationEvent() {}
  }
};
</script>

<style scoped>
.search_box_context {
  text-align: center;
}
/* 搜索输入栏 */
#search_keyword {
  border: none;
  margin: auto;
  height: 100%;
  width: 100%;
  min-height: 44px;
  padding: 0 8px;
  transition: all 0.5s;
  font-size: 16px;

  outline: none;
  box-sizing: content-box;
  vertical-align: top;
}

/* 搜索框装饰器 */
.input_wapper {
  overflow: hidden;
  border-radius: 2px;
  box-shadow: 0 0 4px #555353;
  background-color: #fff;
  transition: all 0.5s;
}
.input_wapper:hover {
  margin: 0 4px;
  box-shadow: 0 0 8px #4f73d8;
  border-radius: 1px;
}

/* logo 文字 */
.search_box_logo {
  color: #262626;
  display: block;
  min-height: 40px;
  background: url("../assets/title_logo2.svg") no-repeat left center;

  white-space: nowrap;
  text-indent: 110%;
  text-transform: capitalize;
  overflow: hidden;

  margin: 12px 0px;
  /* transition: all 0.4s; */
}

/* .search_box_logo:hover {
  margin-left: 2px;
} */
.search_wapper {
  width: 100%;
}

@media (max-width: 768px) {
  /* 手机屏幕缩小logo */
  .search_box_logo {
    margin: 8px 0px;
    min-height: 30px;
    height: 30px;
  }
}
</style>
