"use strict";
//服务层 工具类


let timeoutTask = null

export default {
  // XSS 转义函数
  encode: function (html) {
    const rstr = html.replace(/&/gim, '&amp;')
      .replace(/</gim, '&lt;')
      .replace(/>/gim, '&gt;')
      .replace(/"/gim, '&quot;')
      .replace(/'/gim, '&apos;')
      .replace(/ /gim, '&nbsp;');
    return rstr;
  },
  encodejs: function (text) {
    const rstr = text
      .replace(/"/gim, '\\"')
      .replace(/'/gim, "\\'");
    return rstr;
  },
  decode: function (text) {
    const str = text
      .replace(/&lt;/gim, '<')
      .replace(/&gt;/gim, '>')
      .replace(/&quot;/gim, '"')
      .replace(/&apos;/gim, "'")
      .replace(/&nbsp;/gim, ' ')
      .replace(/<script>/gim, "")
      .replace(/&amp;/gim, '&');
    return str;
  },
  throttle: function (fn, delay) {
    let prev = Date.now()
    return function () {
      let now = Date.now()
      if (now - prev > delay) {
        fn()
        prev = Date.now()
      }
    }
  },
  debounce: function (fn, wait) {
    if (timeoutTask !== null) clearTimeout(timeoutTask)
    timeoutTask = setTimeout(fn, wait)
  }
}