<template>
  <div class="sp_news_panel layout_phone_none" v-show="isDisplay">
    <div class="sp_news_channel">
      <ul>
        <li v-for="item in channel" :key="item.name" @click="onChannelClick(item)">
          <span v-if="item.active">
            <b v-text="item.name"></b>
          </span>
          <span v-else v-text="item.name"></span>
        </li>
        <li>
          <a>更多资讯</a>
        </li>
        <!-- 换一换按钮 -->
        <li class="news_refresh_button" @click="onRefreshNext()">
          <a>
            <svg
              t="1573291881964"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="2716"
              width="12"
              height="12"
            >
              <path
                d="M132.266667 486.826667H128a32 32 0 0 1-26.88-36.693334 416 416 0 0 1 781.226667-128 32.426667 32.426667 0 0 1-13.653334 42.666667 32 32 0 0 1-42.666666-13.653333 352.426667 352.426667 0 0 0-661.333334 108.8 31.573333 31.573333 0 0 1-32.426666 26.88z"
                fill="#2c2c2c"
                p-id="2717"
              />
              <path
                d="M896 213.333333v140.8a42.666667 42.666667 0 0 1-42.666667 38.4 42.666667 42.666667 0 0 1-42.666666-38.4V134.826667A81.493333 81.493333 0 0 1 896 213.333333z"
                fill="#2c2c2c"
                p-id="2718"
              />
              <path
                d="M505.6 913.493333a415.146667 415.146667 0 0 1-369.92-225.706666 32.426667 32.426667 0 0 1 13.653333-42.666667 32 32 0 0 1 42.666667 14.08A352.426667 352.426667 0 0 0 853.333333 549.973333a32 32 0 0 1 63.146667 9.386667 418.56 418.56 0 0 1-410.88 354.133333z"
                fill="#2c2c2c"
                p-id="2719"
              />
              <path
                d="M132.266667 789.333333v-117.333333a32.426667 32.426667 0 0 1 32-32 32.426667 32.426667 0 0 1 32 32V853.333333a64 64 0 0 1-64-64z"
                fill="#2c2c2c"
                p-id="2720"
              />
            </svg>
            换一换
          </a>
        </li>
      </ul>
    </div>
    <div class="sp_news_list">
      <ul>
        <li v-for="item in article" :key="item.title">
          <a
            v-text="item.title"
            :href="item.link"
            :alt="item.title"
            :title="item.title"
            target="_blank"
          ></a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Vue from "vue";

const BASE_NEWS_URL = "http://47.93.47.107:24444";
const PAGE_LEN = 15;

const vmDataModel = {
  channel: [
    { name: "推荐", active: true },
    { name: "新华社", active: false },
    { name: "人民网", active: false },
    { name: "国务院", active: false },
    { name: "外交部", active: false },
    { name: "商务部", active: false },
    { name: "上证交易所", active: false }
  ],
  article: [],
  page: 0,
  isDisplay: true
};

export default {
  name: "NewsPanel",
  props: {},
  data() {
    return vmDataModel;
  },
  methods: {
    async onChannelClick(item) {
      this.page = 0;
      for (const v of this.channel) {
        v.active = false;
      }
      item.active = true;
      await this.requsetArticle(item.name);
    },
    async requsetArticle(reference, page = 0) {
      if (reference === "推荐") {
        reference = "";
        page = 1;
      }
      this.article = [];
      const result = await Vue.axios.get(
        `${BASE_NEWS_URL}/article?limit=${PAGE_LEN}&reference=${reference}&page=${page}`
      );
      const news_list = result.data;
      if (news_list.length == 0) {
        this.article.push({
          title: "暂无更多新闻",
          link: ""
        });
        return;
      }
      let iz = news_list.length;
      let mo = news_list.length % 3;
      for (const v of news_list) {
        iz--;
        if (iz >= mo) {
          this.article.push(v);
        }
      }
    },
    async onRefreshNext() {
      for (const v of this.channel) {
        if (v.active) {
          this.page += 1;
          await this.requsetArticle(v.name, this.page);
        }
      }
    },
    setDisplay(bool) {
      this.isDisplay = bool;
    }
  },
  // 组件挂载事件
  mounted() {
    this.requsetArticle("", 0);
  }
};
</script>

<style scoped>
.sp_news_panel {
  /* 保持上一组件位置 */
  margin: auto;
  margin-top: 36px;

  background-color: #d2d2d21c;
  overflow: hidden;
  text-align: left;
  max-width: 750px;
  border-radius: 4px;
  padding: 8px 24px;
  color: #0f0f0f;
  opacity: 1;

  transition: all 1s;
}

.sp_news_channel {
  border-bottom: 1px solid rgb(204, 204, 204);
}

.sp_news_channel > ul > li {
  display: inline-block;
  /* margin-right: 8px; */
  list-style-type: none;
  cursor: pointer;
  margin: 6px 6px;
  font-size: 14px;
}
.sp_news_channel > ul > li:hover {
  font-weight: 800;
}

.news_refresh_button {
  float: right;
  font-size: 14px;
  color: #0f0f0f;
  padding-right: 2px;
}

.news_refresh_button:hover {
  color: rgb(56, 106, 245);
}

.sp_news_list {
  /* text-align: center; */
  padding-top: 12px;
  padding-left: 3px;
}
.sp_news_list > ul > li {
  margin: 4px 4px;
  display: inline-block;
  font-size: 12px;
  font-weight: 400;
  /* letter-spacing: 0.01px; */

  width: 240px;
  max-width: 240px;
  min-width: 240px;
  text-align: left;

  text-overflow: ellipsis; /*让截断的文字显示为点点。还有一个值是clip意截断不显示点点*/
  white-space: nowrap; /*让文字不换行*/
  overflow: hidden; /*超出要隐藏*/
}
.sp_news_list a {
  text-decoration: none;
  color: #0f0f0f;
}
.sp_news_list a:hover {
  text-decoration: underline;
  color: rgb(64, 106, 243);
}
@media (max-width: 768px) {
  /* .sp_news_panel {
  } */
  .sp_news_list > ul > li {
    width: 100%;
    max-width: 768px;
  }
}
</style>
