"use strict";
import Vue from "vue";

// 百度关键字查询地址
// https://www.baidu.com/su?wd=Hello&cb=callback

export default {

  json(url) {
    return Vue.jsonp(url, {
      callbackQuery: "cb",
      callbackName: "callback"
    });
  },

  keywordTips(keyword) {
    if (keyword != '') {
      return Vue.jsonp("https://www.baidu.com/su", {
        wd: keyword,
        callbackQuery: "cb",
        callbackName: "jsonpCallback"
      });
    }
    return null;
  },

  keywordSearch(type, keyword, args) {
    let text = keyword
    let orginText = keyword;
    let url = null;

    // 搜索记录原文临时保存在本地
    Vue.$cookies.set("keyword", text);

    text = encodeURIComponent(text);
    if (type == 'baidu') {
      url = `https://www.baidu.com/s?ie=UTF-8&wd=${text}`;
    }
    if (type == 'bing') {
      url = `https://cn.bing.com/search?q=${text}&ensearch=${args[0]}`;
    }
    if (type == 'google') {
      url = `https://www.google.com.hk/search?q=${text}`;
    }
    if (type == 'github') {
      url = `https://github.com/search?q=${text}`;
    }
    if (type == 'tieba') {
      url = `https://www.baidu.com/s?ie=UTF-8&wd=${text} inurl:${args[0]}`;
    }
    if (type == 'translate') {
      const regEnglishWord = new RegExp('^[0-9a-zA-Z _]+$', 'igm');
      if (regEnglishWord.test(orginText)) {
        url = `https://translate.google.cn/#view=home&op=translate&sl=auto&tl=zh-CN&text=${text}`
      } else {
        url = `https://translate.google.cn/#view=home&op=translate&sl=auto&tl=en&text=${text}`
      }
    }
    if (type == 'zhihu') {
      url = `https://www.zhihu.com/search?type=content&q=${text}`;
    }
    return url;
  }


}
